body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x:hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2{
  text-align: center;
}


.league-selection-container{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.league-selection-container .league-card-container{
    border:1px solid #ccc;
    padding: 1% 2%;
    margin: 2%;
    cursor:pointer;
    flex:1 1;
}

.league-selection-container .league-card-container .league-name{
    position: relative;
    top: 10%;
    margin: 10% 0;
    text-align: center;
}

.league-selection-container .league-card-container .league-img{
    text-align: center;
}
.league-selection-container .league-card-container:hover{
   -webkit-transform:scale(1.2);
           transform:scale(1.2); 
}

@media (max-width: 960px) {
    .league-selection-container{
        flex-direction: column;
        width: 78%;
        margin: 2% auto;
    }

    .league-selection-container .league-card-container .league-name{
        margin: 0;
    }
}
.league-detail-container{
    width: 100%;
    height: 100vh;
}   

.league-detail-container .left-menu-content{
    width: 200px;
    height: 100%;
    float:left;
    text-align:center;
}

.league-detail-container .main-content{
    width: calc(100% - 200px);
    height: 100%;
    float:left;
}

@media (max-width: 768px){
    .league-detail-container .left-menu-content{
        width: 100%;
        height: unset;
        float: unset;
    }
    
    .league-detail-container .main-content{
        width: 100%;
        height: 100%;
        float:unset;
    }
}
.left-menu-container{
    height: 100%;
    display:flex;
    flex-direction:column;
    justify-content: center;
    border-right: 1px solid #ccc;
}

.left-menu-container .league-card-container{
    border-bottom: 1px solid #ccc;
    border-top: 1px solid #ccc;
    cursor: pointer;
}

.left-menu-container .league-card-container.selected{
    background:lightblue;
}

.left-menu-container .league-card-container .league-img{
    padding: 2% 0;
}

@media (max-width: 768px){
    .left-menu-container{
        flex-direction: row;
    }
    
    .left-menu-container a{
        flex:1 1;
    }
}

@media (max-width: 480px){
    .left-menu-container .league-card-container .league-img img{
        width: 100%;
        height:auto;
    }
}


#team-info-modal {
  width: 50%;
  min-width: 300px;
  max-height: 90%;
  border-radius: 4%;
  padding: 0;
  overflow: auto;
}

#team-info-modal .header-info {
  background: linear-gradient(180deg, #6690ae 0%, #494f55 100%);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 15px 20px;
  border-radius: 8px 8px 0 0;
  position: relative;
  top: -1px;
  color: white;
}

#team-info-modal .header-info .closeButton {
  cursor: pointer;
}

#team-info-modal .detail-info {
  margin: 2% 5%;
  display: flex;
  flex-direction: column;
}
#team-info-modal .detail-info .player {
  font-family: Arial, Helvetica, sans-serif;
  margin: 2px 0;
  cursor: pointer;
}
#team-info-modal .detail-info .player-gk {
  margin-left: 0;
}

#team-info-modal .detail-info .player-def {
  margin-left: 15%;
}

#team-info-modal .detail-info .player-mid {
  margin-left: 30%;
}

#team-info-modal .detail-info .player-fw {
  margin-left: 45%;
}

#team-info-modal .detail-info .player-coach {
  text-align: right;
  margin-right: 5%;
}

#player-info-modal{
    width: 30%;
    min-width: 300px;
    border-radius: 4%;
    padding: 0;
}

#player-info-modal .header-info{
    background: linear-gradient(180deg, #6690AE 0%, #494F55 100%);
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 15px 20px;
    border-radius: 8px 8px 0 0;
    position: relative;
    top: -1px;
    color:white;
}

#player-info-modal .header-info .closeButton{
    cursor: pointer;
}

#player-info-modal .detail-info{
    margin: 5%;
    display: flex;
    flex-direction: row;
}

#player-info-modal .detail-info div{
    display:flex;
    flex-direction:column;
    flex:1 1;
}

#player-info-modal .detail-info div span:nth-of-type(1){
    font-weight: bold;
}

#player-info-modal .detail-info div span:nth-of-type(2){
    color: blueviolet;
    font-weight: bold;
}
.goal-scorer-card-container{
    /* width: 30%; */
    margin: 2%;
}
.goal-scorer-card{
    display:flex;
    flex-direction:row;
    border:1px solid #ccc;
    padding: 2% 4%;
}
.goal-scorer-card .sorting-number{
    margin-right: 2%;
}
.goal-scorer-card .name-informations{
    display:flex;
    flex-direction:column;
    flex-grow: 1;
}

.goal-scorer-card .name-informations .player-name{
    font-weight: bold;
    cursor: pointer;
}
.fixture-container {
  margin: 0 2%;
}

.fixture-container .match-container {
  padding: 1%;
  border: 1px solid #ccc;
  /* width: 35%; */
}

.fixture-container .match-container.FINISHED {
  background-color: #00800080;
}

.fixture-container .match-container.IN_PLAY {
  animation: blink-animation 2s steps(5, start) infinite;
  -webkit-animation: blink-animation 2s steps(5, start) infinite;
}

@keyframes blink-animation {
  to {
    visibility: hidden;
  }
}
@-webkit-keyframes blink-animation {
  to {
    visibility: hidden;
  }
}
.fixture-container .match-container.POSTPONED {
  background-color: #ff000080;
}

.fixture-container .match-container.SCHEDULED {
  background-color: #ffa50080;
}

.fixture-container .match-container .match {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.fixture-container .match-container .match .score {
  margin: 0 2%;
}

.fixture-container .match-container .match .status {
  flex-grow: 1;
  text-align: right;
}

@media (max-width: 480px){
  .fixture-container .match-container .match .home-team{
    flex:2 1;
  }

  .fixture-container .match-container .match .score{
    margin:0;
    flex:1 1;
  }

  .fixture-container .match-container .match .away-team{
    flex:2 1;
  }

  .fixture-container .match-container .match .status{
    flex:1 1;
  }


}
